* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.flickity-gallery {
  height: 50%;
  background: #eee;
  position: relative;
  overflow-x: clip;
}

.flickity-gallery-cell {
  width: 28%;
  counter-increment: gallery-cell;
  background: none;
  margin-right: 10px;
}

.flickity-gallery-cell.is-selected {
  background: none;
}

.flickity-gallery-cell b {
  vertical-align: middle;
  font-family: Courier New, Courier, monospace;
  font-size: 70px;
  line-height: 200px;
}

.flickity-prev-next-button {
  display: inline-block;
}

.flickity-button:first-of-type {
  margin-right: 10px;
}

.flickity-button {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #000;
  border-radius: 10px;
  outline: none;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
}

.flickity-button:hover, .flickity-button:focus {
  border: 1px solid #ff6d00;
}

.flickity-button:after {
  height: 10px;
  width: 10px;
  background-color: #000;
  position: absolute;
}

